import { IconButton, Typography, makeStyles } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import React from "react";

const useStyles = makeStyles((theme) => ({
  root: {
    border: `1px dashed ${theme.colors.default}`,
    padding: 16,
    borderRadius: 16,
  },
  input: {
    display: "none",
  },
  content: {
    textAlign: "center",
    position: "relative",
  },
  placeholder: {
    color: theme.colors.default,
    marginBottom: 20,
  },
  button: {
    color: theme.colors.black,
    fontSize: 20,
    backgroundColor: theme.colors.white,
    borderRadius: 16,
    display: "inline-flex",
    width: 150,
    height: 40,
    justifyContent: "center",
    alignItems: "center",
    cursor: "pointer",
    transition: "all 0.4s",
    "&:hover": {
      opacity: 0.4,
    },
  },
  img: {
    height: 150,
    border: `1px solid ${theme.colors.default}`,
  },
  remove: {
    position: "absolute",
    right: 0,
    top: 0,
    padding: 8,
    border: `1px solid ${theme.colors.default}`,
    color: theme.colors.default,
  },
}));

interface IProps {
  value: {
    file: File | null;
    fileUrl: string;
  };
  placeholder: string;
  onChange: (file: File | null) => void;
  size: {
    width: number;
    height: number;
  };
  id: string;
}

export const ImageUpload = (props: IProps) => {
  const { id, onChange, placeholder, size, value } = props;
  const classes = useStyles();

  const onChangeFiles = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      onChange(event.target.files[0]);
      (event.target as any).value = null;
    }
  };

  return (
    <div className={classes.root}>
      <input
        accept="image/png,image/jpeg,image/jpg"
        className={classes.input}
        id={id}
        onChange={onChangeFiles}
        type="file"
      />
      {!value.file && (
        <div className={classes.content}>
          <Typography className={classes.placeholder}>{placeholder}</Typography>
          <label className={classes.button} htmlFor={id}>
            Upload Image
          </label>
        </div>
      )}
      {value.file && value.fileUrl && (
        <div className={classes.content}>
          <Typography className={classes.placeholder}>{placeholder}</Typography>
          <img alt="img" className={classes.img} src={value.fileUrl} />
          <IconButton className={classes.remove} onClick={() => onChange(null)}>
            <CloseIcon />
          </IconButton>
        </div>
      )}
    </div>
  );
};
