import { Hidden, IconButton, makeStyles } from "@material-ui/core";
import clsx from "clsx";
import React, { useState } from "react";
import { matchPath, NavLink, useHistory } from "react-router-dom";
import useCommonStyles from "style/common";
import MenuIcon from "@material-ui/icons/Menu";
import MenuOpenIcon from "@material-ui/icons/MenuOpen";

import { AccountInfo } from "../AccountInfo";
import { transparentize } from "polished";
import { useScrollYPosition } from "helpers/useScrollPosition";

const useStyles = makeStyles((theme) => ({
  root: {
    position: "fixed",
    zIndex: 10,
    top: 0,
    left: 0,
    right: 0,
    height: theme.custom.appHeaderHeight,
    padding: "8px 0",
    transition: "all 0.4s",
    "&.header-white": {
      backgroundColor: theme.colors.black,
    },
  },
  content: {
    height: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
  },
  mainContent: {
    display: "flex",
    alignItems: "center",
  },
  logo: {
    zIndex: 100,
    "& img": {
      height: 48,
    },
  },
  navContent: {
    marginRight: 40,
    display: "flex",
    alignItems: "center",
    "& > * + *": {
      marginLeft: 32,
    },
    [theme.breakpoints.down("sm")]: {
      marginTop: 24,
      padding: "0 16px",
      flexDirection: "column",
      marginRight: 0,
      "& > * + *": {
        marginLeft: 0,
        marginTop: 16,
      },
    },
  },
  navItem: {
    color: theme.colors.white,
    textDecoration: "none",
    transition: "all 0.4s",
    fontWeight: 500,
    "&:hover": {
      color: theme.colors.primary,
    },
    "&.active": {
      color: theme.colors.primary,
    },
    [theme.breakpoints.down("sm")]: {
      display: "flex",
      alignItems: "center",
      width: "100%",
      height: 60,
      padding: 20,
      "&:hover": {
        backgroundColor: theme.colors.fourth,
        color: theme.colors.white,
      },
      "&.active": {
        color: theme.colors.black,
        backgroundColor: theme.colors.primary,
      },
    },
  },
  mobileNavbar: {
    position: "fixed",
    top: 0,
    bottom: 0,
    width: theme.custom.appNavbarWidth,
    backgroundColor: theme.colors.black,
    boxShadow: `0 0 2px 0 ${transparentize(0.6, theme.colors.white)}`,
    transition: "all 0.7s",
    left: -theme.custom.appNavbarWidth,
    "&.visible": {
      left: 0,
    },
    paddingTop: theme.custom.appHeaderHeight,
  },
  menu: {
    marginLeft: 16,
    color: theme.colors.white,
  },
}));

const items = [
  { id: "new-ido", title: "New IDO", href: "/new-ido" },
  { id: "new-sec-ido", title: "New Sec IDO", href: "/new-sec-ido" },
  { id: "new-league", title: "New League", href: "/new-league" },
];

interface IState {
  navbarVisible: boolean;
}

export const Header = () => {
  const classes = useStyles();
  const commonClasses = useCommonStyles();
  const [state, setState] = useState<IState>({ navbarVisible: false });
  const yPosition = useScrollYPosition();
  const history = useHistory();

  const setNavbarVisible = (navbarVisible: boolean) => {
    setState((prev) => ({ ...prev, navbarVisible }));
  };

  const renderMenuItems = () => {
    return (
      <>
        {items.map((item) => (
          <NavLink
            className={classes.navItem}
            key={item.id}
            to={item.href}
            isActive={() =>
              !!matchPath(history.location.pathname, {
                path: item.href,
                exact: true,
              })
            }
          >
            {item.title}
          </NavLink>
        ))}
      </>
    );
  };

  return (
    <div className={clsx(classes.root, yPosition > 40 ? "header-white" : "")}>
      <div className={clsx(commonClasses.content, classes.content)}>
        <div className={classes.logo}>
          <NavLink to="/">
            <img alt="logo" src="/assets/logo.png" />
          </NavLink>
        </div>
        <div className={classes.mainContent}>
          <Hidden smDown>
            <div className={classes.navContent}>{renderMenuItems()}</div>
          </Hidden>
          <AccountInfo />
          <Hidden mdUp>
            <IconButton
              className={classes.menu}
              onClick={() => setNavbarVisible(!state.navbarVisible)}
            >
              {state.navbarVisible ? <MenuOpenIcon /> : <MenuIcon />}
            </IconButton>
          </Hidden>
        </div>
      </div>
      <Hidden mdUp>
        <div
          className={clsx(
            classes.mobileNavbar,
            state.navbarVisible ? "visible" : ""
          )}
        >
          <div className={classes.navContent}>{renderMenuItems()}</div>
        </div>
      </Hidden>
    </div>
  );
};
